import { EventObject, MachineConfig, actions, StateSchema, MachineOptions, sendParent, assign } from 'xstate';


const recordingEffect = (ctx: any, evt: any) => {
    ctx.mediaRecorder.start()
}

export interface RecorderContext {
    content: string;
    canRecord: boolean;
    mediaRecorder: any;
    blob: any;
    audioURL: any;
}

export interface RecorderStateSchema extends StateSchema {
    states: {
        idle: {};
        recording: {};
        stopped: any;
        recorded: any;
        sending: {};
    }
}

export type EventId = "STOP" | "RECORD" | "PLAY" | "PAUSE" | "SEND" | "TRASH" | "CLOSE"

export interface RecorderEvent extends EventObject {
    type: EventId
    payload: any
}
export interface RecorderMachineOptions extends MachineOptions<RecorderContext, RecorderEvent> {

}
export const recorderMachineOptions: RecorderMachineOptions = {
    actions: {
        recordingEffect
    },
    services: {
        mutateMyAnswers: () => new Promise((res, rej) => setTimeout(() => res(), 1000)),
    },
    guards: {
    },
    activities: {},
    delays: {}
}

export const recorderMachineConfig: MachineConfig<RecorderContext, RecorderStateSchema, RecorderEvent> = {
    id: "recorderMachine",
    on: {
        CLOSE: {
            actions: sendParent("CLOSE")
        }
    },
    initial: 'idle',
    context: {
        content: "",
        canRecord: false,
        mediaRecorder: null,
        blob: null,
        audioURL: ""
    },
    states: {
        idle: {
            invoke: {
                src: () => {
                    return new Promise((res, rej) => {
                        const constraints = { audio: true };
                        return navigator.mediaDevices.getUserMedia(constraints)
                            .then((stream) => res(stream))
                            .catch((err) => rej(err))
                    })
                },
                onDone: {
                    actions: assign((ctx, evt) => ({ canRecord: true, mediaRecorder: new MediaRecorder(evt.data) }))
                },
                onError: {
                    actions: assign((ctx, evt) => ({ canRecord: false }))
                },
            },
            on: {
                RECORD: {
                    target: "recording",
                    cond: (ctx, evt) => ctx.canRecord
                },
            },
        },
        stopped: {
            invoke: {
                src: (ctx, evt) => new Promise((res, rej) => {
                    ctx.mediaRecorder.stop();
                    ctx.mediaRecorder.ondataavailable = (e: any) => {
                        const chunks = [];
                        chunks.push(e.data);
                        res({
                            blob: new Blob(chunks, { 'type': 'audio/ogg; codecs=opus' }),
                            audioURL: window.URL.createObjectURL(e.data)
                        })
                    }
                }),
                onDone: {
                    target: "recorded",
                    actions: assign((_ctx, { data: { blob, audioURL } }) => ({ blob, audioURL }))
                },
            },
        },
        recorded: {
            initial: "paused",
            states: {
                playing: { on: { PAUSE: "paused" } },
                paused: { on: { PLAY: "playing" } }
            },
            on: {
                SEND: {
                    target: "sending",
                },
                TRASH: {
                    target: "idle",
                    actions: (ctx, evt) => ctx.content = ""
                }
            }
        },
        recording: {
            onEntry: ["recordingEffect"],
            // after: {
            //     500: [
            //         {
            //             target: "recording",
            //             cond: (ctx) => ctx.content.length < 10
            //         },
            //         {
            //             target: "recorded"
            //         }
            //     ]
            // },
            on: {
                STOP: {
                    target: "stopped",
                },
            }
        },
        sending: {
            invoke: {
                src: "mutateMyAnswers",
                onDone: {
                    target: "idle"
                },
                onError: {
                    target: 'recorded'
                }
            },
        },
    }
}