import React, { useState, EventHandler, ReactEventHandler, useEffect } from 'react';
import { useMachine } from 'use-machine';
import { MachineConfig, StateSchema, EventObject } from 'xstate';
import { assign, sendParent } from 'xstate/lib/actions';

const mutateMyAnswers = () => new Promise((res, rej) => setTimeout(() => {
    rej()
}, 2000));

const Abcer = () => {
    const [inputValue, setInputValue] = useState("");

    const { send, state: { value, matches }, context } = useMachine<AbcerStateSchema, AbcerEvent, AbcerContext>(abcerMachineConfig, { services: { mutateMyAnswers } }, { content: "" });

    const handleInputChange = (e: any) => {
        if (matches("idle") || matches("failure")) {
            console.log(value)
            send("TYPED");
        }
        setInputValue(e.target.value);
    }
    const handleSend = () => {
        console.log('sent')
        send({ type: "SEND", payload: inputValue })
    }
    const handleTrash = () => {
        setInputValue("")
        send("TRASH")
    }
    useEffect(() => {
        if (matches("success")) {
            console.log("success");
        } else if (matches("failure")) {
            console.log("failure", context);
        }
    }, [value])

    return (
        <>
            <input value={inputValue} onChange={handleInputChange} />
            {(matches("typed") || matches("failure")) && <>
                <button onClick={handleSend}>send</button>
                <button onClick={handleTrash}>trash</button>
            </>}
            {matches("failure") &&
                <span>error. pls retry</span>
            }
        </>
    );
}

export default Abcer;


interface AbcerContext {
    content: string;
}

interface AbcerStateSchema extends StateSchema {
    states: {
        idle: {};
        typed: {};
        loading: {};
        success: {};
        failure: any
    }
}

type EventId = "TYPED" | "SEND" | "TRASH" | "CLOSE"

interface AbcerEvent extends EventObject {
    type: EventId;
    payload: any
}

export const abcerMachineConfig: MachineConfig<AbcerContext, AbcerStateSchema, AbcerEvent> = {
    id: 'abcerMachine',
    initial: 'idle',
    on: {
        CLOSE: {
            actions: sendParent("CLOSE")
        }
    },
    states: {
        idle: {
            on: {
                TYPED: "typed"
            }
        },
        typed: {
            on: {
                SEND: {
                    target: "loading",
                    actions: assign((ctx, evt: AbcerEvent) => ({ content: evt.payload }))
                },
                TRASH: {
                    target: "idle",
                    actions: assign((ctx, evt) => ({ content: "" }))
                }
            }
        },
        loading: {
            invoke: {
                src: "mutateMyAnswers",
                onDone: {
                    target: "success"
                },
                onError: {
                    target: "failure",
                }
            },
        },
        success: {
        },
        failure: {
            on: {
                SEND: 'loading',
                TRASH: {
                    target: "idle",
                    actions: assign((ctx, evt) => ({ content: "" }))
                },
                TYPED: "typed"
            }
        }
    }
}