import "./App.css"
import React from 'react';
import Recorder from './Recorder'
import Abcer from './Abcer'
import { useMachine } from 'use-machine';
import { MachineConfig, StateSchema, EventObject, Machine } from 'xstate';
import { recorderMachineConfig } from './RecordMachine'
import { abcerMachineConfig } from './Abcer'

interface StudentStateSchema extends StateSchema {
  states: {
    idle: {};
    recording: any;
    abcing: any
  }
}

const studentMachineConfig: MachineConfig<{}, StudentStateSchema, EventObject> = {
  id: "studentMachine",
  initial: "idle",
  states: {
    idle: {
      on: {
        ABC: "abcing",
        RECORD: "recording"
      }
    },
    recording: {
      invoke: {
        src: Machine(recorderMachineConfig)
      },
      on: {
        CLOSE: "idle"
      }
    },
    abcing: {
      invoke: {
        src: Machine(abcerMachineConfig)
      },
      on: {
        CLOSE: "idle"
      }
    }
  }
}

function App() {
  const { state: { matches }, send } = useMachine(studentMachineConfig, {}, { content: "" })

  return (
    <div className="App">
      <div className="topSide">
        <div className="board">
          <img alt="board" />
        </div>

        <div className="launcher">
          {matches("idle") && <>
            <button onClick={() => send("ABC")}>ABC</button>
            <button onClick={() => send("RECORD")}>RECORD</button>
          </>}
        </div>
      </div>

      {!matches("idle") && <div className="controlBoard">
        <button className="close-btn" onClick={() => send("CLOSE")}>X</button>
        {matches("abcing") && <Abcer />}
        {matches("recording") && <Recorder />}
      </div>}
    </div>
  );
}

export default App;
